@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

.App {
  text-align: center;
}

.App-header {
  overflow: hidden;
  background-image: url(./assets/Background.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App {
  text-align: center;
}

.Link {
  width: 74px;
  height: 74px;
  border-radius: 15px;
  margin: 9px;
  border: none;
  background-color: transparent;
}

.Link:hover {
  opacity: 50%;
}

.Dock {
  display: flex;
  flex-direction: row;
  padding: 9px;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 24px;
}

.InsideDock {
  display: flex;
}

.image {
  border-radius: 15px;
  width: 74px;
  margin-left: -6px;
  margin-top: -1.4px;
}

.Desc {
  font-size: 30px;
}

.Name {
  font-size: 30px;
  font-weight: 800;
}

.Head {
  font-family: 'Pretendard';
  padding-bottom: 10px;
}

@media screen and (max-width: 760px) {
  .Dock {
    flex-direction: column;
  }
  .Desc {
    font-size: 20px;
  }
  .Name {
    font-size: 20px;
  }
}